body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
/* .navbar {
  width: 100% !important;
  border: 2px solid red;
} */

.navbar-nav {
	z-index: 200;
}
.navbar-brand {
	color: #007bff !important;
	font-size: x-large;
	font-weight: bold;
}

.home-wrapper {
	/* border: 2px solid red; */
	position: relative;
}
.home-text {
	text-align: justify;
	font-size: large;
}
.img-home {
	border-radius: 5%;
	width: 95%;
	height: auto;
	z-index: 1000;
	/* border: 2px solid red; */
}

.mycustom-jumbotron {
	width: 800px;
	margin: auto !important;
	background-color: white;
	padding: 40px 0 0px 0;
}

.link-container {
	margin-bottom: 20px;
}

.footer {
	margin-top: 30px !important;
	background: #f8f9fa !important;
	z-index: 200 !important;
}

.canvas-ref {
	position: absolute;
	top: 0;
	left: 0;
	/* z-index: 100; */
}

.card {
	width: 400px !important;
	margin: 0 10px 50px 0 !important;
	border-top: 5px solid #007bff;
	border-radius: 5px;
	position: relative;
}
.img-holder {
	position: absolute;
	width: 100%;
	top: -40px;
}
.img-holder img {
	border-radius: 50%;
	border: 3px solid rgb(129, 129, 139);
	width: 65px;
	height: 65px;
}
.card-title {
	/* background-color: rgb(245, 248, 253); */
	font-size: 14px;
	height: 35px;
	overflow: auto;
	margin-bottom: 2px;
}

.img-card {
	width: 16%;
}

.card-body {
	margin-top: 20px !important;
}
.card-text {
	height: 190px;
	overflow-y: scroll;
	text-align: justify;
	padding-right: 10px;
	font-size: small;
}
.publisher-name {
	font-size: 12px;
	color: #ff5e00;
	height: 20px;
	margin-bottom: 15px;
}

.researchgate {
	font-size: 30px;
	color: #007bff !important;
}
/* width */
::-webkit-scrollbar {
	width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px grey;
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #007bff;
	border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #007bff;
}

.cv {
	background-color: slategrey;
	padding-top: 10px;
}
